.main_container {
  background-color: #0000;
  background-image: url("circle.414cf44f.svg"), radial-gradient(#08143a, #0b1b38, #06030e, #06030e, #06030e);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto, auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  padding: 8rem 0;
}

.main_container .circle {
  outline-offset: 4rem;
  border: 1px dashed #ffffff1a;
  border-radius: 50%;
  outline: 1px dashed #ffffff1a;
  width: 40rem;
  height: 40rem;
  padding: 6rem;
  position: relative;
}

.main_container .circle .innerCircle {
  border: 1px dashed #ffffff1a;
  border-radius: 50%;
  width: calc(100% - 8rem);
  height: calc(100% - 8rem);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.arrowBtn {
  color: #ff9217;
  background: linear-gradient(90.33deg, #fb892024 0%, #ff4d011b 100%);
  border-radius: 2rem;
}

.featuringText {
